/**
 * 活动项目： 入口固定要求引入此文件
 */
// 全局css
import '@lib/styles/reset.css';
import '@lib/styles/common.css';
// 全局打点
import '@lib/monitor';
// URLSearchParams polyfill
import 'url-search-params-polyfill';
// global error catch
// import '@lib/logger/script-error';
// callShare兼容
import '@lib/callShare';
import cookie from 'js-cookie';

export const isDev = process.env.NODE_ENV !== 'production';
export const isTest = typeof window !== 'undefined'
    && window.location.host.indexOf('test.') !== -1;
export const onlineDebug = typeof window !== 'undefined'
    && window.location.search.indexOf('huajiao_debug=true') !== -1;

function initEruda() {
  const script = document.createElement('script');
  script.src = '//cdn.jsdelivr.net/npm/eruda';
  document.body.appendChild(script);
  script.onload = () => {
    window.eruda.init();
  };
}
// 测试域名 或者cookie注入了调试标记 或者 url参数有 huajiao_debug=true
if (cookie.get('huajiao_debug') === 'eruda' || onlineDebug) {
  initEruda();
}

export default {
  isDev,
  isTest,
};
