import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const plugins = []; // 插件列表

if (module.hot) {
    /* eslint global-require: "off" */
    const createLogger = require('vuex/dist/logger');
    plugins.push(createLogger());
}

export default new Vuex.Store({
    strict: true,
    plugins,
    modules: {
    },
});
