import Vue from 'vue';
import Router from 'vue-router';
import Index from '@/js/page/Index';
import Home from '@/js/page/Home';
import Air from '@/js/page/Air';
import Bus from '@/js/page/Bus';
import Dolphin from '@/js/page/Dolphin';
import Near from '@/js/page/Near';
import DonotPass from '@/js/page/DonotPass';

Vue.use(Router);

const routes = [
    {
        path: '*',
        name: 'Index',
        component: Index,
    },
    {
        path: '/home',
        name: 'Home',
        component: Home,
    },
    {
        path: '/air',
        name: 'Air',
        component: Air,
    },
    {
        path: '/bus',
        name: 'Bus',
        component: Bus,
    },
    {
        path: '/dolphin',
        name: 'Dolphin',
        component: Dolphin,
    },
    {
        path: '/near',
        name: 'Near',
        component: Near,
    },
    {
        path: '/donotPass',
        name: 'DonotPass',
        component: DonotPass,
    }
];
const router = new Router({
    routes,
    scrollBehavior(to, from, savedPosition) {
        if (savedPosition) {
            return savedPosition;
        }
        return { x: 0, y: 0 };
    },
    mode: 'hash',
});

// 设置路由标题
router.beforeEach((to, from, next) => {
    if (to.meta.title) {
        document.title = to.meta.title;
    }
    next();
});

export default router;
