/*
 * 安卓CallShare兼容 请尽量使用JSBridge中的方法, 此文件中的方法不再维护
 * 解决安卓客户端将targetSDK提升到27后, 安卓8.0及以后系统中出现webview中Callshare对象不可用的问题
 */

/* eslint-disable */
if (typeof navigator !== 'undefined') {
  var userAgent = navigator.userAgent.toLowerCase();

  function isAndroid() {
    return /android|linux/.test(userAgent);
  }
  function isHuajiao() {
    return /huajiao/.test(userAgent);
  }

  if (isAndroid() && isHuajiao() && typeof window.CallShare == 'undefined') {
    window.CallShare = {
      PageLoaded: function() {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'PageLoaded',

              args: [],
            })
        );
      },

      activeFollow: function(arg0) {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'activeFollow',

              args: [arg0],
            })
        );
      },

      activeFollowCancel: function(arg0) {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'activeFollowCancel',

              args: [arg0],
            })
        );
      },

      auth: function(arg0) {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'auth',

              args: [arg0],
            })
        );
      },

      callShare: function(arg0, arg1, arg2, arg3, arg4) {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'callShare',

              args: [arg0, arg1, arg2, arg3, arg4],
            })
        );
      },

      callShareTo: function(arg0, arg1, arg2, arg3, arg4, arg5) {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'callShareTo',

              args: [arg0, arg1, arg2, arg3, arg4, arg5],
            })
        );
      },

      follow: function(arg0, arg1) {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'follow',

              args: [arg0, arg1],
            })
        );
      },

      forceFinish: function(arg0) {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'forceFinish',

              args: [arg0],
            })
        );
      },

      login: function() {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'login',

              args: [],
            })
        );
      },

      modifyUser: function() {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'modifyUser',

              args: [],
            })
        );
      },

      reloadUrl: function(arg0) {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'reloadUrl',

              args: [arg0],
            })
        );
      },

      showMyIncome: function() {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'showMyIncome',

              args: [],
            })
        );
      },

      zhima: function(arg0) {
        prompt(
          'MyApp:' +
            JSON.stringify({
              obj: 'CallShare',

              func: 'zhima',

              args: [arg0],
            })
        );
      },
    };
  }
}
