const MONITOR_URL = '//s.ssl.qhimg.com/static/32ea1a8972889917/monitor_analytic.js';

/**
 * [监听全局按钮点击，统计日志]
 * @description  按钮标签添加自定义属性 data-monitor="abc" (abc 为统计字段)
 * @example  <p data-monitor="abc" >确认按钮</p>
 */
function globalMonitor() {
  document.addEventListener('click', (e) => {
    const $this = e.target;
    const cValue = $this.getAttribute('data-monitor');
    const cText = $this.innerHTML;
    if (cValue) {
      window.monitor.log(
        {
          cId: cValue,
          c: cText,
        },
        'click',
      );
      console.log('monitor');
    }
  });
}

function monitorAsyncLoad() {
  const script = document.createElement('script');

  script.addEventListener(
    'load',
    () => {
      window.monitor
        .setProject('QH_5_3')
        .getTrack()
        .getClickAndKeydown()
        .getClickHeatmap(10, 1);
      globalMonitor();
    },
    false,
  );

  script.src = MONITOR_URL;

  document.body.appendChild(script);
}
if (typeof window !== 'undefined') {
  if (window.monitor) {
    globalMonitor();
  } else {
    // DOMContentLoaded 后加载统计脚本
    document.addEventListener(
      'DOMContentLoaded',
      () => {
        console.log('DOMContentLoaded');
        monitorAsyncLoad();
      },
      false,
    );
  }
}
